@import "../../style.scss";

.discussionPost {
  @include themify($themes) {
    -webkit-box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
    -moz-box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
    box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
    border-radius: 8px;
    background-color: themed("bg");
    color: themed("textColor");
    cursor: pointer;
    transition: 0.2s ease;

    .postContainer {
      .user {
        padding: 16px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .userInfo {
          display: flex;
          gap: 8px;
          img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
            border: 2px solid #d9d5cd;
            cursor: pointer;
            transition: 0.2s;
          }
          img:hover {
            filter: brightness(95%);
            transition: 0.1s;
          }

          .details {
            display: flex;
            justify-content: space-around;
            flex-direction: column;

            .name {
              font-weight: bold;
              font-size: 14px;
              cursor: pointer;
            }
            .name:hover {
              text-decoration: underline;
            }
            .date {
              font-size: 12px;
              color: themed("textColorSoft");
            }
          }
        }

        .moreActions {
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 0.7rem;
          position: relative;

          button {
            position: absolute;
            top: 130%;
            right: 5%;
            transform: translate(-50%, -50%);
            border: none;
            background-color: #f0544f;
            padding: 8px 1rem;
            border-radius: 8px 0px 8px 8px;
            cursor: pointer;
            color: white;
            transition: 0.1s;
          }

          button:hover {
            filter: brightness(95%);
            transition: 0.1s;
          }

          button:active {
            transform: translate(-50%, -50%);
            scale: 98%;
            background-color: #f0544f;
            filter: brightness(95%);
          }

          .pin {
            display: none;
            padding: 8px 9px;
            background-color: transparent;
            border-radius: 50%;
            transition: 0.2s;
            @include mobile {
              display: block;
            }
          }
          .moreOptions {
            cursor: pointer;
            padding: 8px 9px;
            font-size: 1.2rem;
            background-color: transparent;
            border-radius: 50%;
            transition: 0.2s;
          }
          .moreOptions:hover {
            background-color: themed("bgSoft");
            transition: 0.2s;

            @include mobile {
              background-color: transparent;
            }
          }
        }
        .moreActions *:active {
          background-color: themed("bgSoft");
          transition: 0s;
          scale: 93%;
          transform: perspective(1px) translateZ(0);
          backface-visibility: hidden;
        }
      }

      .postContent {
        p {
          font-size: 0.875rem;
          padding: 0px 16px;
        }

        img {
          background-color: themed("bg");
          width: 100%;
          max-height: 500px;
          object-fit: contain;
          margin-top: 10px;

          border-top: 1px solid #e5e5e3b6;
          border-bottom: 1px solid #e5e5e3b6;
          cursor: pointer;
          transition: 0.2s;
        }
        img:hover {
          transition: 0.2s;
          background-color: themed("bgSoft");
        }
      }
    }
  }
}

.discussionPost:hover {
  filter: brightness(95%);
  scale: 101%;
  transition: 0.2s ease;
}
.discussionPost:active {
  scale: 100%;
  transition: 0.2s ease;
}
