@import "../../style.scss";

.comments {
  @include themify($themes) {
    width: 95%;
    margin: auto;
    font-size: 14px;
    img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      object-fit: cover;
    }

    .write {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;
      margin: 20px 0px;

      input {
        flex: 5;
        padding: 10px;
        border: 1px solid themed("border");
        border-radius: 200px;
        background-color: transparent;
        color: themed("text");
      }

      button {
        border: none;
        background-color: #000;
        color: white;
        padding: 10px;
        cursor: pointer;
        border-radius: 50%;

        display: flex;
        justify-content: center;
        align-items: center;
      }
    }

    .comment {
      margin: 30px 0px;
      display: flex;
      justify-content: space-between;
      gap: 20px;

      .commentInfo {
        flex: 5;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 3px;

        span {
          font-weight: 500;
        }

        p {
          color: themed("textColorSoft");
        }
      }

      .date {
        flex: 1;
        align-self: center;
        color: gray;
        font-size: 12px;
      }
    }
  }
}
